import {
	AfterViewInit,
	ChangeDetectionStrategy,
	ChangeDetectorRef,
	Component,
	ComponentFactoryResolver,
	ElementRef,
	forwardRef,
	Inject,
	Renderer2,
	ViewEncapsulation
} from '@angular/core';

import { FabricNotificationService } from './fabric-notification.service';
import { Theme } from '../../themes/theme';
import { themeToken } from '../../themes/theme.token';
import { DialogComponent } from '../common/dialog.component';
import { FabricNotification } from './fabric-notification';


@Component({
	template: `
		<div class="gui-notifications-container">
			<div *ngFor="let notification of notifications; let index=index">
				<gui-notification [notification]="notification"
								  (onNotificationClose)="removeNotification($event)">
				</gui-notification>
			</div>
		</div>
	`,
	styleUrls: [
		'./fabric-notification.ngx.scss'
		// './themes/fabric-notifications.dark.ngx.scss'
	],
	changeDetection: ChangeDetectionStrategy.OnPush,
	encapsulation: ViewEncapsulation.None
})
export class FabricNotificationsContainerComponent extends DialogComponent implements AfterViewInit {
	notifications: Array<FabricNotification> = [];

	constructor(public readonly changeDetectorRef: ChangeDetectorRef,
				private readonly componentFactoryResolver: ComponentFactoryResolver,
				private readonly elRef: ElementRef,
				renderer: Renderer2,
				@Inject(themeToken) theme: Theme,
				@Inject(forwardRef(() => FabricNotificationService)) private readonly notificationsService: FabricNotificationService) {
		super(elRef, renderer, theme);
	}

	ngAfterViewInit() {
		this.addTheme();
	}

	removeNotification(index: number): void {
		this.notifications = this.notifications.filter((notification: FabricNotification) => notification.index !== index);
		this.changeDetectorRef.detectChanges();
		this.checkNotificationsLength();
	}

	checkNotificationsLength(): void {
		if (this.notifications.length === 0) {
			this.notificationsService.close();
		}
	}

}
